export function addReaction(reaction, octokit, context) {
    /**
     * Adds a reaction to the triggering issue.
     */
    octokit.reactions.createForIssue({
        owner: context.repo.owner,
        repo: context.repo.repo,
        issue_number: context.issue.number,
        content: reaction,
    });
}
export function addLabels(labels, octokit, context) {
    /**
     * Adds labels to the triggering issue.
     */
    octokit.issues.addLabels({
        owner: context.repo.owner,
        repo: context.repo.repo,
        issue_number: context.issue.number,
        labels: labels,
    });
}
